@import url("https://fonts.googleapis.com/css2?family=Gothic+A1:wght@600&family=Mulish:ital,wght@0,200;1,400&family=Roboto+Mono:wght@500&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Gothic A1", sans-serif;
}
body {
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: calc(100vh);
}
@keyframes animationDown {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(5px);
  }
  60% {
    transform: translateY(3px);
  }
}
h2 {
  font-size: 40px;
  color: #393c41;
  text-transform: capitalize;
}
p {
  font-size: 14px;
  color: #5c5d61;
  text-transform: capitalize;
}
a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #181b21;
  font-size: 14px;
  font-family: "Gothic A1", sans-serif;
}
